import React from 'react'

function Navbar(props) {
  return (
    <div className='flex p-4 justify-between items-center w-full z-[100] absolute' style={{ backgroundImage:props.movie}}>
      <h1 className='navigation-title text-4xl text-red-600 font-bold float-left'>Netflix</h1>
 
      <div>
        <button className='navigation-button pr-4 text-white'>Sign in</button>
        <button className='navigation-buttonsignup bg-red-600 py-2 px-6 rounded text-white'>Sign up</button>
      </div>
    </div>

  )
}

export default Navbar