import React from 'react'
import axios from 'axios'
import { useEffect } from 'react';
import { useState } from 'react';
import "../Css/row.css"
function Row({title,URL}) {
    console.log(title, URL,"i am data");
const[movie,setmovie]=useState([])
    useEffect(() => {

        axios.get(URL).then((resp) => {
            setmovie(resp.data.results);


        })

    }, [])
    console.log(movie ,"i am a movie");
    
  return (
    <div>

        <h1 Style={{color:"white",position:"absolute" ,float:"left" }} className="text-2xl  text-white top-[77px]">{title}</h1>
<div className="my-4 "> 
        <div className='flex flex-row flex-nowrap  sm:grid-cols-2 overflow-scroll overflow-y-hidden  newclass'  style={{position:"relative", }}>
            {                   
            movie?.map((movie,id)=>
            (

                
                // <div  key={id} className="flex-shrink-0 mx-2 my-6" style={{height: '100px', width: '230px', backgroundImage: `url('https://image.tmdb.org/t/p/original/${movie?.backdrop_path}')`,backgroundSize:"cover"}}>
             <div  key={id} className="flex-shrink-0 mx-2">
             <p className=' w-auto text-red-700 font-bold hover:bg-red-[600 ]'  style={{position: 'absolute',zIndex: "12opx ",textAlign: 'center'}}>{movie?.original_title }</p>
             <img src={`https://image.tmdb.org/t/p/w500/${movie?.backdrop_path}` } className="z-[100px] hover:z-[1000px]" style={{"height": "100px",width:"250px",backgroundSize:"cover",cursor:"pointer"}} /> 
            </div>
        ))    
    }
    </div>
    </div>
    </div>
  )
}

export default Row