import React from 'react'
import Navbar from './Navbar'
import axios from "axios"
import {useState,useEffect} from 'react'
import { getMouseEventOptions } from '@testing-library/user-event/dist/utils'
function Main() {
const[popularmovie,setpopular]=useState([])
const[upcommingame,setupcomming]=useState([])
const[style1,setstyle]=useState(
{
  margin:"0px"
}
)
  function handlemouse()
  {
        setstyle({
          margin:"12px",
          zindex:"-2",
          backgroundColor:'red',

        })
  }
  function leave()
  {
    setstyle({
      margin:"0px",
      backgroundColor:'blue',

    })
  } 

useEffect(()=>
{
   axios.get("https://api.themoviedb.org/3/movie/popular?api_key=f6ec15ac9175d25fb791c9b4f4d17ffa&language=en-US&page=1").then((resp)=>
   {
        setpopular(resp.data.results);
        
   })
},[]) 

const rdm=popularmovie[Math.floor(Math.random()*popularmovie.length)]   
console.log(popularmovie);
return (
    <>
    <div className='h-[550px] w-full text-white bg-cover' style={{backgroundImage:`url('https://image.tmdb.org/t/p/original/${rdm?.backdrop_path}')`}}>
        <div className='details ' style={{width: '100%', height:'200%'}}>
           <div className=' absolute w-full h-full '></div>
            {/* <img classname="w-full object-contain w-full justify-center" src={`https://image.tmdb.org/t/p/original/${rdm?.backdrop_path}`} alt="" style={{height:"60%"}}/> */}
                <h1 className="font-bold absolute justify-cente text-white top-[32%]  text-3xl md:text-5xl left-3">{rdm?.title}</h1>
                <div className='absolute top-[45%] mx-4'>
                    
                 <button className="border px-8 py-2 rounded bg-slate-50 text-black hover:active:bg-red-600">Play</button>
                 <button className="border mx-5 py-2 px-8 bg-gray-600 rounded text-white hover:active:bg-white">Watch later</button>
                </div>
                <p className='text-white  xl:max-w-[35%] lg:max-w-[50%] md:max-w-[70%] px-1  absolute top-[55%]'>{rdm?.overview}</p>
            </div>

            

    
    </div>
    </>
  )
}

export default Main